import AnimContext from "animContext";
import AuthContext from "authContext";
import { Field, Form, Formik } from "formik";
import { Fragment, useContext } from "react";
import { useDispatch, useSelector } from "react-redux";
import { addPost } from "toolkit/postsSlice";
import { insertPost } from "toolkit/usersSlice";

function Add() {
  const dispatch = useDispatch();
  const auth = useContext(AuthContext);
  const anim = useContext(AnimContext);
  const posts = useSelector(state => state.posts);

  const form = {
    initialValues: {
      title: '',
      body: ''
    },
    validate: ({ title, body }) => {
      const errors = {};
      if (title === '') {
        errors.title = 'No title!';
      }
      if (body === '') {
        errors.body = 'No body!';
      }
    },
    onSubmit: ({ title, body }) => {
      if (title && body) {
        const post = {
          title,
          body,
          likes: [],
          comments: []
        };
        for (let i = 0; i < posts.length; i++) {
          if (posts[i].title === title) {
            anim.setCurrentClass('animate-alert-down');
            anim.setError('Post alerady exists');
            return;
          }
        }
        dispatch(addPost(post));
        dispatch(insertPost({ user: auth.currentUser, title }));
      } else {
        anim.setCurrentClass('animate-alert-down');
        anim.setError('Empty field!');
      }
    }
  };

  return (
    <Fragment>
      <h2 style={{ textAlign: 'center', margin: '20px 0' }}>
        Add Post
      </h2>
      <Formik {...form}>
        <Form className="add">
          <Field
            name="title"
            placeholder="Title"
            className="add__field add__title"
          /><br/>
          <Field
            name="body"
            placeholder="Body"
            className="add__field add__body"
          /><br/>
          <button
            type="submit"
            className="add__submit"
          >
            Create
          </button>
        </Form>
      </Formik>
    </Fragment>
  );
}

export default Add;
