const { createSlice } = require("@reduxjs/toolkit");

const postsSlice = createSlice({
  name: 'posts',
  initialState: [
    {
      title: 'Title 1',
      body: `My name is Daler`,
      likes: 10,
      comments: [{
        author: 'Daler',
        body: 'Good good good!'
      }]
    },
    {
      title: 'Title 2',
      body: `My name is Daler`,
      likes: 23,
      comments: [
        {
          author: 'Daler',
          body: 'Not Bad!'
        },
        {
          author: 'Aziz',
          body: 'Shit!'
        }
      ]
    }
  ],
  reducers: {
    addPost: (state, { payload }) => {
      state.push(payload);
    },
    addComment: (state, { payload }) => {
      console.log(payload);
      const { title, comment } = payload;
      for (let i = 0; i < state.length; i++) {
        if (state[i].title === title) {
          state[i].comments.push(comment);
        }
      }
    },
    like: (state, { payload }) => {
      console.log(payload);
      const { title } = payload;
      for (let i = 0; i < state.length; i++) {
        if (state[i].title === title) {
          state[i].likes++;
        }
      }
    }
  }
});

export const { addPost, addComment, like } = postsSlice.actions;

export default postsSlice.reducer;
