import AnimContext from "animContext";
import { useContext } from "react";

function Alert(props) {
  const anim = useContext(AnimContext);

  const close = () => {
    anim.setCurrentClass('animate-alert-up');
  };

  return (
    <div
      className={`alert ${anim.currentClass}`}
    >
      <h3>
        {anim.error}
      </h3>
      <button
        className={`alert__close`}
        onClick={close}
      >
        Close
      </button>
    </div>
  );
}

export default Alert;
