import AuthContext from "authContext";
import { useContext, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { addComment, like } from 'toolkit/postsSlice';

function Home() {
  const [comment, setComment] = useState('');
  const [opened, setOpened] = useState(false);
  const dispatch = useDispatch();
  const auth = useContext(AuthContext);
  const allPosts = useSelector(state => state.posts);
  const allUsers = useSelector(state => state.users);

  const handleCommentSubmit = (e, title) => {
    e.preventDefault();
    const commentObj = {
      author: auth.currentUser,
      body: comment
    };
    dispatch(addComment({
      title,
      comment: commentObj
    }));
  };

  const handleLike = (title) => {
    dispatch(like({ title }));
  };

  if (!auth.loggedin) {
    return (
      <h1 style={{ textAlign: 'center', marginTop: '50px' }}>
        Not logged in!
      </h1>
    );
  }

  let userPosts = [];

  for (let user of allUsers) {
    if (auth.currentUser === user.username) {
      userPosts = user.posts;
    }
  }

  const selectedPosts = allPosts.filter(post => {
    if (userPosts.includes(post.title)) {
      return true;
    }
    return false;
  });

  const renderedPosts = selectedPosts.map(post => {
    return (
      <div className="posts__post" key={post.title}>
        <h2 className="post__title">
          {post.title}
        </h2>
        <div className="post__body">
          {post.body}
        </div>
        <span
          className="post__likes"
          onClick={() => handleLike(post.title)}
        >
          <i className="fas fa-heart"></i>
        </span> {post.likes}<br/>
        {/* {opened ? <h3>Comments:</h3> : null} */}
        <ul className="post__comments">
          {opened ?
            post.comments.map(comment => (
              <li className="comments__comment" key={post.comments.indexOf(comment)}>
                <h3 className="comment__author">{comment.author}</h3>
                <div className="comment__body">{comment.body}</div>
              </li>
            )) : null
          }
        </ul>
        {opened ?
          <form>
            <input
              value={comment}
              onChange={(e) => setComment(e.target.value)}
              placeholder="Comment"
            />
            <button
              className="post__add-btn"
              onClick={(e) => handleCommentSubmit(e, post.title)}
            >
              Add comment
            </button>
          </form> : null
        }
        {!opened ?
          <div
            className="post__open-comments-btn"
            onClick={() => setOpened(true)}
          >
            Open comments
          </div>
          :
          <div
            className="post__close-comments-btn"
            onClick={() => setOpened(false)}
          >
            Close comments
          </div>
        }
      </div>
    );
  });

  return (
    <div className="home">
      <h2 className="home__label">Your posts</h2>
      <ul className="posts">
        {renderedPosts}
      </ul>
      
    </div>
  );
}

export default Home;
