import { useState } from "react";
import { useSelector } from "react-redux";

function useAuth() {
  const [loggedin, setLoggedin] = useState(true);
  const [currentUser, setCurrentUser] = useState('Daler');

  const users = useSelector(state => state.users);

  const handleLogin = (user, password) => {
    let success = false;
    for (const u of users) {
      if (u.username === user) {
        if (u.password === password) {
          success = true;
        }
      }
    }
    if (success) {
      setCurrentUser(user);
      setLoggedin(true);
    }
    return success;
  }

  const handleLogout = () => {
    setLoggedin(false);
    setCurrentUser(null);
  }

  return {
    loggedin: loggedin,
    currentUser: currentUser,
    login: (user, password) => handleLogin(user, password),
    logout: () => handleLogout()
  };
}

export default useAuth;
