import { Redirect, Route, Switch } from "react-router";
import Add from "./pages/Add";
import Home from "./pages/Home";
import Login from "./pages/Login";
import Profile from "./pages/Profile";
import Register from "./pages/Register";
import Users from "./pages/Users";

function Main() {
  return (
    <main className="main">
      <Switch>
        <Route path="/" exact>
          <Redirect
            to={{
              pathname: "/home"
            }}
          />
        </Route>
        <Route path="/home">
          <Home />
        </Route>
        <Route path="/users">
          <Users />
        </Route>
        <Route path="/login">
          <Login />
        </Route>
        <Route path="/register">
          <Register />
        </Route>
        <Route path="/add">
          <Add />
        </Route>
        <Route path="/profile">
          <Profile />
        </Route>
      </Switch>
    </main>
  );
}

export default Main;
