import { createSlice } from '@reduxjs/toolkit';

export const usersSlice = createSlice({
  name: 'users',
  initialState: [
    {
      username: 'Daler',
      password: '111',
      posts: ['Title 2'],
      followings: []
    },
    {
      username: 'Aziz',
      password: '222',
      posts: [],
      followings: []
    }
  ],
  reducers: {
    addUser: (state, { payload }) => {
      state.push(payload);
    },
    insertPost: (state, { payload }) => {
      console.log(payload);
      const { user, title } = payload;
      for (let i = 0; i < state.length; i++) {
        if (state[i].username === user) {
          state[i].posts.push(title);
        }
      }
    },
    follow: (state, { payload }) => {
      console.log(payload);
      const { from, to } = payload;
      for (let i = 0; i < state.length; i++) {
        if (state[i].username === from) {
          state[i].followings.push(to);  
        }
      }
    },
    unfollow: (state, { payload }) => {
      console.log(payload);
      const { from, to } = payload;
      let followings = [];
      for (let i = 0; i < state.length; i++) {
        if (state[i].username === from) {
          followings = state[i].followings;
          const newFollowings = followings.filter(f => {
            if (f === to) {
              return false;
            }
            return true;
          });
          state[i].followings = newFollowings;    
        }
      }
    }
  }
});

export const { addUser, follow, unfollow, insertPost } = usersSlice.actions;

export default usersSlice.reducer;
