import AnimContext from "animContext";
import { Field, Form, Formik } from "formik";
import { Fragment, useContext } from "react";
import { useDispatch, useSelector } from "react-redux";
import { addUser } from "toolkit/usersSlice";

function Register() {
  const dispatch = useDispatch();
  const anim = useContext(AnimContext);
  const users = useSelector(state => state.users);

  const form = {
    initialValues: {
      username: '',
      password: ''
    },
    onSubmit: ({ username, password }) => {
      if (username && password) {
        const user = {
          username,
          password,
          posts: [],
          followings: []
        };
        for (let i = 0; i < users.length; i++) {
          if (users[i].username === username) {
            anim.setError('User already exists!');
            anim.setCurrentClass('animate-alert-down');
            return;
          }
        }
        dispatch(addUser(user));
      } else {
        anim.setError('Emply field!');
        anim.setCurrentClass('animate-alert-down');
      }
    },
    validate: (values) => {
      const errors = {};
      return errors;
    }
  };

  return (
    <Fragment>
      <h2 style={{ textAlign: 'center', margin: '20px 0' }}>
        Register form
      </h2>
      <Formik {...form}>
        <Form className="register">
          <Field
            name="username"
            placeholder="Username"
            className="register__field register__username"
          /><br/>
          <Field
            name="password"
            placeholder="Password"
            className="register__field register__password"
          /><br/>
          <button
            type="submit"
            className="register__submit"
          >
            Create
          </button>
        </Form>
      </Formik>
    </Fragment>
  );
}

export default Register;
