import AnimContext from "animContext";
import AuthContext from "authContext";
import { Field, Form, Formik } from "formik";
import { Fragment, useContext } from "react";
import { useHistory } from "react-router";

function Login() {
  const auth = useContext(AuthContext);
  const anim = useContext(AnimContext);
  const history = useHistory();

  const form = {
    initialValues: {
      username: '',
      password: ''
    },
    validate: (values) => {
      const errors = {};
      return errors;
    },
    onSubmit: (values) => {
      if (values.username && values.password) {
        const success = auth.login(values.username, values.password);
        if (success) {
          history.push('/profile');
        } else {
          alert('no such user');
        }
      } else {
        anim.setError('Emply field!');
        anim.setCurrentClass('animate-alert-down');
      }
    }
  };

  return (
    <Fragment>
      <h2 style={{ margin: '20px 0', textAlign: 'center' }}>
        Login Page
      </h2>
      <Formik {...form}>
        <Form className="login">
          <Field
            name="username"
            placeholder="Username"
            className="login__field"
          /><br/>
          <Field
            name="password"
            placeholder="Password"
            className="login__field"
          /><br/>
          <button
            type="submit"
            className="login__submit"
          >
            Login
          </button>
        </Form>
      </Formik>
    </Fragment>
  );
}

export default Login;
