import AnimContext from "animContext";
import AuthContext from "authContext";
import { useContext } from "react";
import { useDispatch, useSelector } from "react-redux";
import { follow, unfollow } from "toolkit/usersSlice";

function Users() {
  const dispatch = useDispatch();
  const auth = useContext(AuthContext);
  const anim = useContext(AnimContext);
  const users = useSelector(state => state.users);

  const handleFollow = (username) => {
    if (auth.loggedin) {
      dispatch(follow({ to: username, from: auth.currentUser })); 
    } else {
      showAlert();
    }
  };

  const handleUnfollow = (from, to) => {
    if (auth.loggedin) {
      dispatch(unfollow({ from, to }));
    } else {
      showAlert();
    }
  };

  const showAlert = () => {
    anim.setError('Not logged in!');
    anim.setCurrentClass('animate-alert-down');
  };

  const renderedUsers = users.map(user => {
    let userFollowings = [];
    for (let i = 0; i < users.length; i++) {
      if (auth.currentUser === users[i].username) {
        userFollowings = users[i].followings;
        break;
      }
    }

    const followBtn = (
      <button
        className="user__follow"
        onClick={() => handleFollow(user.username)}
      >
        Follow
      </button>
    );

    const unfollowBtn = (
      <button
        className="user__unfollow"
        onClick={() => handleUnfollow(auth.currentUser, user.username)}
      >
        Unfollow
      </button>
    );

    if (auth.currentUser !== user.username) {
      return (
        <li className="users__user" key={user.username}>
          <h3>{user.username}</h3>
          {userFollowings.includes(user.username) ? 
            unfollowBtn : followBtn
          }
        </li>
      );  
    }
  });

  return (
    <>
      <h2 style={{ textAlign: 'center', margin: '20px 0' }}>Users</h2>
      <ul className="users">
        {renderedUsers}
      </ul>
    </>
  );
}

export default Users;
