import AuthContext from "authContext";
import { useContext } from "react";
import { useSelector } from "react-redux";

function Profile() {
  const auth = useContext(AuthContext);
  const users = useSelector(state => state.users);

  if (!auth.loggedin) {
    return (
      <h1 style={{ textAlign: 'center', marginTop: '50px' }}>
        Not logged in!
      </h1>
    );
  }

  let currentUser;
  for (let i = 0; i < users.length; i++) {
    if (users[i].username === auth.currentUser) {
      currentUser = users[i];
    }
  }

  let followers = 0;
  for (let i = 0; i < users.length; i++) {
    if (users[i].followings.includes(currentUser.username)) {
      followers++;
    }
  }

  console.log(currentUser);

  return (
    <>
      <h2 style={{ textAlign: 'center', margin: '20px 0' }}>
        Profile
      </h2>
      <div className="profile">
        <table className="profile__table">
          <tbody>
            <tr className="profile__username">
              <td>
                Username
              </td>
              <td>
                {currentUser.username}
              </td>
            </tr>
            <tr className="profile__followings">
              <td>
                Followings
              </td>
              <td>
              {currentUser.followings.length}
              </td>
            </tr>
            <tr className="profile__followers">
              <td>
                Followers
              </td>
              <td>
                {followers}
              </td>
            </tr>
          </tbody>
        </table>
        {/* <div className="profile__username">
          Username: {currentUser.username}
        </div>
        <div className="profile__followings">
          Followings: {currentUser.followings.length}
        </div>
        <div className="profile__followers">
          Followers: {followers}
        </div> */}
      </div>
    </>
  );
}

export default Profile;
